import { mkdirp } from 'fs-extra';
import * as execa from 'execa';

export interface NpmInstallOptions {
  path: string;
  installPackages: string[];
}

/**
 * Will internally use the npm cache, so no need to worry about http requests from npm that are not needed.
 */
export async function npmInstall({ path, installPackages }: NpmInstallOptions) {
  await mkdirp(path);
  await execa('npm', ['init', '-y'],{cwd:path});
  await execa('npm', ['install', ...installPackages],{cwd:path});
}
