import {
  mkdirp,
  pathExists,
  readdir,
  readFile,
  stat,
  Stats,
  writeFile
} from 'fs-extra';
import * as JSZip from 'jszip';
import { mapKeys, toPairs } from 'lodash';
import { basename, dirname, join, resolve } from 'path';
import * as lockfile from 'proper-lockfile';
import { npmInstall, NpmInstallOptions } from './npm/install';
const rimraf = require('rimraf');

interface InstallOptions {
  zipPath: string;
  currentState: { [name: string]: string };
  installPackages: string[];
}

interface DependencyContent {
  [relPath: string]: Buffer;
}

interface FilesFromFolder {
  [relPath: string]: Buffer;
}

interface DependencyTree {
  [relPath: string]: {
    name: string;
    version: string;
  };
}

interface PackageLock {
  dependencies?: {
    [name: string]: {
      version: string;
      dependencies?: PackageLock;
    };
  };
}

function getDependencyTreeInternal(
  result: DependencyTree,
  obj: PackageLock,
  rel?: string
) {
  for (const [name, { version, dependencies }] of toPairs(obj.dependencies)) {
    const fp = rel ? join(rel, 'node_modules', name) : name;
    result[fp] = {
      name,
      version
    };
    if (dependencies) {
      getDependencyTreeInternal(result, dependencies, fp);
    }
  }
  return result;
}

async function getDependencyTree(packageLock: string): Promise<DependencyTree> {
  const obj = JSON.parse(
    (await readFile(packageLock)).toString()
  ) as PackageLock;
  return getDependencyTreeInternal({}, obj);
}

async function writeFiles(path: string, files: FilesFromFolder) {
  console.log('writing', path);

  for (const [relPath, content] of toPairs(files)) {
    const fullPath = join(path, relPath);
    await mkdirp(dirname(fullPath));
    await writeFile(fullPath, content);
  }
}

async function readFilesRecursive(
  root: string,
  skipCallback?: (relPath: string, stat: Stats) => boolean
) {
  const result: FilesFromFolder = {};
  const files = await getFilesRecursive(root, skipCallback);
  for (const relPath of files) {
    const buffer = await readFile(resolve(root, relPath));
    result[relPath] = buffer;
  }
  return result;
}

async function getFilesRecursive(
  root: string,
  skipCallback?: (relPath: string, stat: Stats) => boolean,
  parentRel?: string
): Promise<string[]> {
  const result: string[] = [];
  const files = await readdir(parentRel ? join(root, parentRel) : root);
  for (const file of files) {
    const relPath = parentRel ? join(parentRel, file) : file;
    const fp = join(root, relPath);
    const stats = await stat(fp);
    if (!skipCallback || !skipCallback(relPath, stats)) {
      if (stats.isFile()) {
        result.push(relPath);
      } else {
        result.push(...(await getFilesRecursive(root, skipCallback, relPath)));
      }
    }
  }
  return result;
}

function includeInOptimizedDependency(pathOrName: string): boolean {
  return true; // todo
}

function toZip(files: FilesFromFolder) {
  var zip = new JSZip();
  for (const [relPath, content] of toPairs(files)) {
    zip.file(relPath, content);
  }
  return zip.generateAsync({ type: 'nodebuffer' });
}

async function createOptimizedDependency(
  root: string,
  parentRel?: string
): Promise<DependencyContent> {
  const files = await getFilesRecursive(root, (relPath, stats) => {
    return stats.isDirectory() && basename(relPath) === 'node_modules';
  });
  const result: DependencyContent = {};
  for (const relPath of files) {
    if (relPath.toLowerCase().endsWith('.js')) {
      const buffer = await readFile(resolve(root, relPath));
      const source = buffer.toString();
      const babel = require('@babel/core');
      // console.log('Transpiling',relPath);
      const transpiled = babel.transform(source, {
        presets: [
          [
            require('@babel/preset-env'),
            {
              targets: {
                ie: '10'
              }
            }
          ]
        ] // todo besser machen
      }).code as string;
      // console.log('Minifying',relPath);
      const UglifyJS = require('uglify-es');
      const minified = UglifyJS.minify(transpiled).code as string; // todo speed up uglify
      result[relPath] = Buffer.from(minified, 'utf8');
    } else if (includeInOptimizedDependency(relPath)) {
      result[relPath] = await readFile(resolve(root, relPath));
    }
  }

  return result;
}
const MODULE_CACHE = join(__dirname, 'optmodules_cache'); // todo

function getCacheDir(name: string, version: string) {
  console.log('getcachedir', MODULE_CACHE, name, version);
  return join(MODULE_CACHE, name, version);
}

async function tryGetOptimizedDependencyFromCache(
  name: string,
  version: string
): Promise<DependencyContent | void> {
  console.log('Checking cache for', name, version, getCacheDir(name, version));
  if (await pathExists(getCacheDir(name, version))) {
    console.log('Got from cache', name, version);
    return await readFilesRecursive(getCacheDir(name, version));
  }
}

async function setOptimizedDependency(
  name: string,
  version: string,
  content: DependencyContent
) {
  console.log('setting cache dep');
  await writeFiles(getCacheDir(name, version), content);
}

async function getOrCreateOptimizedDependency(
  name: string,
  version: string,
  originalContentPath: string
) {
  let result = await tryGetOptimizedDependencyFromCache(name, version);
  if (!result) {
    const dir = getCacheDir(name, version);
    await mkdirp(dirname(dir));
    const file = `${dir}.lock`;
    await writeFile(file, '');
    const release = await lockfile.lock(file);
    try {
      result = await tryGetOptimizedDependencyFromCache(name, version);
      if (!result) {
        result = await createOptimizedDependency(originalContentPath);
        await setOptimizedDependency(name, version, result);
      }
    } finally {
      await release();
    }
  }
  return result;
}
// todo https://www.npmjs.com/package/p-limit

async function getModulesFromNpmInstallation(installPath: string) {
  const result: FilesFromFolder = {};
  const deps = await getDependencyTree(join(installPath, 'package-lock.json'));
  for (const [relPath, { name, version }] of toPairs(deps)) {
    const optimizedDep = await getOrCreateOptimizedDependency(
      name,
      version,
      resolve(installPath, 'node_modules', relPath)
    );
    Object.assign(result, mapKeys(optimizedDep, (v, k) => join(relPath, k)));
  }
  return result;
}

async function getZippedModulesFromInstall(options: NpmInstallOptions) {
  const modules = await getModulesFromInstall(options);
  const zip = await toZip(modules);
  return zip;
}

async function getModulesFromInstall(options: NpmInstallOptions) {
  await npmInstall(options);
  const modules = await getModulesFromNpmInstallation(options.path);
  return modules;
}

export async function installModules({
  destPath,
  ...options
}: NpmInstallOptions & { destPath: string }) {
  const modules = await getModulesFromInstall(options);
  await new Promise((resolve, reject) =>
    rimraf(destPath, (err: unknown) => (err ? reject(err) : resolve()))
  );
  await writeFiles(destPath, modules);
}

export async function install({
  zipPath,
  currentState,
  installPackages
}: InstallOptions) {
  const workingRoot = ''; // todo
  const installPath = join(workingRoot, 'npm_install');
  const existingPackages = toPairs(currentState).map(
    ([name, version]) => `${name}@${version}`
  );
  await npmInstall({
    path: installPath,
    installPackages: [...existingPackages, ...installPackages]
  });
}
